import React from 'react'
import Image from 'next/image'
import { FaPlayCircle } from 'react-icons/fa'
import { CiBoxList } from 'react-icons/ci'
import Collectio from '@/components/Dashboard/Gems'

const Home = () => {
    return (
        <div className="relative w-full h-[70vh] sm:h-[80vh] md:h-screen">
            {/* Background Image */}
            <Image
                src="/thor.jpg"
                alt="Thor Ragnarok"
                fill
                priority
                className="object-cover "
            />

            {/* Overlay Content */}
            <div className="absolute inset-0 flex items-center">
                <div className="px-6 sm:px-10 md:px-20 max-w-2xl">
                    <div className="flex flex-col gap-4 sm:gap-5">

                        {/* Netflix Logo */}
                        <Image
                            src="/netflix.png"
                            alt="Netflix"
                            width={90}
                            height={90}
                            className="w-16 sm:w-20"
                        />

                        {/* Title */}
                        <h1 className="text-3xl sm:text-4xl md:text-6xl font-bold text-white">
                            Thor: Ragnarok
                        </h1>

                        {/* Tagline */}
                        <p className="text-sm sm:text-base md:text-lg text-white font-bold ">
                            Unlimited movies, TV shows, and more.
                        </p>

                        {/* Description */}
                        <p className="text-sm sm:text-base text-white/90 leading-relaxed">
                            Thor: Ragnarok is a 2017 American superhero film based on the Marvel
                            Comics character Thor, produced by Marvel Studios.
                        </p>

                        {/* Buttons */}
                        <div className="flex flex-wrap gap-4 mt-2">
                            <button className="bg-white flex items-center gap-2 text-black font-medium py-2 px-6 sm:px-8 rounded hover:bg-[#e50914] hover:text-white transition">
                                Play <FaPlayCircle />
                            </button>

                            <button className="bg-slate-400 flex items-center gap-2 text-white font-medium py-2 px-6 sm:px-8 rounded hover:bg-slate-600 transition">
                                My List <CiBoxList />
                            </button>
                        </div>
                    </div>
                </div>
            </div>

            <div className="absolute bottom-0 w-full h-[6rem] bg-gradient-to-t from-black to-transparent z-10 overflow-visible"></div>

            <div className='absolute flex flex-col gap-4  top-[60vh] sm:top-[70vh] md:top-[90vh] w-full z-[1000] overflow-visible'>
                <Collectio />
                <Collectio />
            </div>
        </div>

    )
}

export default Home